<template>
  <v-sheet tile>
    <v-sheet
      class="mt-6"
      tile
      v-if="!this.isLoading"
    >
      <header-title class="mt-6 mb-2">
        {{ title1_text }}
      </header-title>
      <header-small-title class="my-4">
        {{ title2_text }}
      </header-small-title>

      <product-slide-view
        class="mt-2 p-1"
        :class="{
          'ml-0': $vuetify.breakpoint.smAndDown,
          'ml-n4': $vuetify.breakpoint.mdAndUp,
        }"
        :cards="this.exchangeItemVariantCards"
        :on-card-selected="onCardSelected"
        :default-value="defaultValue"
      />
      <v-row
        :class="{
          'mt-3': $vuetify.breakpoint.smAndDown,
          'mt-5': $vuetify.breakpoint.mdAndUp,
        }"
      >
        <v-col
          :class="{
            'pb-0 ': $vuetify.breakpoint.smAndDown,
            'half-width': $vuetify.breakpoint.mdAndUp
          }"
          order-md="2"
          class="d-flex justify-end"
        >
          <action-button
            @click.stop="togglePdpPage(true)"
            tertiary-color
            :loading="isLoading"
            :block="$vuetify.breakpoint.smAndDown"
            large
            text
          >
            {{ action2_Text }}
          </action-button>
          <PDPPage
            :visible="showPDPPage"
            @close="togglePdpPage(false)"
            :return-reason-clicked="returnReasonClicked"
          />
        </v-col>
        <v-col
          :class="{
            'half-width': $vuetify.breakpoint.mdAndUp
          }"
          order-md="1"
        >
          <action-button
            @click.stop="onClickMethod"
            primary-color
            :loading="isLoading || isMapLoading"
            :block="$vuetify.breakpoint.smAndDown"
            large
            :disabled="isDisabled"
          >
            {{ this.selectedItem ? action1_Selected_Text : action1_Text }}
          </action-button>
        </v-col>
      </v-row>
    </v-sheet>
    <div v-else>
      <v-skeleton-loader type="card" />
    </div>
  </v-sheet>
</template>

<script>
import { mapGetters } from 'vuex'
import {
  saveSelectedItemToStore,
  getDefaultValue,
  getColorOptionsForColorView
} from '@/controllers/ExchangesController'
import HeaderSmallTitle from '@/components/molecules/HeaderSmallTitle.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { CONSTANTS } from '@/constants'
import { PAGE_TITLES } from 'branding/strings.json'

export default {
  name: 'ExchangeColorView',
  components: {
    ProductSlideView: () =>
      import('@/components/organism/ProductSlideView.vue'),
    PDPPage: () => import('@/components/pages/PDPPage.vue'),
    ActionButton: () => import('@/components/atoms/ActionButton.vue'),
    HeaderTitle,
    HeaderSmallTitle
  },
  data: () => ({
    showPDPPage: false,
    title1_text: "Let's try a different color",
    title2_text: 'Select a color',
    action1_Text: 'Select a color to exchange',
    action1_Selected_Text: 'Exchange for this color',
    action2_Text: 'See all sizes and colors...',
    selectedItem: null,
    isDisabled: true,
    ACTION_TYPE: CONSTANTS.ACTION_TYPE
  }),
  props: {
    returnReasonClicked: {
      type: Function,
      required: true
    },
    isMapLoading: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      isLoading: 'exchange/isLoading',
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems',
      exchangeList: 'order/exchangeList'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    },
    exchangeItemVariantCards () {
      return getColorOptionsForColorView()
    },
    defaultValue () {
      return getDefaultValue(this.ACTION_TYPE.COLOR)
    }
  },
  methods: {
    onCardSelected (card) {
      if (!card.disabled) {
        this.selectedItem = card
        this.updateIsDisabled()
      }
    },
    togglePdpPage (opening) {
      if (opening) {
        this.showPDPPage = true
        document.title = PAGE_TITLES.PDP_PAGE
      } else {
        this.showPDPPage = false
        document.title = PAGE_TITLES.EXCHANGE
      }
    },
    onClickMethod (event) {
      saveSelectedItemToStore(this.selectedItem)
      this.returnReasonClicked()
      this.$emit('click', event)
    },
    updateIsDisabled () {
      this.isDisabled = !(
        this.selectedItem &&
        !this.selectedItem.disabled &&
        !this.isLoading
      )
    },
    updateCardSubtitles () {
      this.exchangeItemVariantCards.forEach(card => {
        card.subtitle = card.subtitle ? card.size + ' ' + card.subtitle : card.size
      })
    }
  },
  mounted () {
    this.selectedItem = this.defaultValue
    this.updateCardSubtitles()
    this.updateIsDisabled()
  }
}
</script>
<style scoped>
.half-width {
  max-width: 50%
}
</style>
